import React from 'react';
import {Shopping} from "../Authroute"



export class ProductDetail extends React.Component{
constructor(props)
{
	super(props);
	this.state={IsLoaded:false};
}
GoBack()
{
	this.props.history.goBack();
}
async componentDidMount(){
	const res= await fetch('https://jsonplaceholder.typicode.com/photos/'+this.props.match.params.id);
   const dat = await res.json();
   this.setState({pItem:dat,IsLoaded:true});
 }

render(){
	return (
	<div>
	<h1>{this.props.match.params.id}</h1>
	<br></br>

	{this.state.IsLoaded && <DetProduct prod={this.state.pItem} > </DetProduct>}


	<CartOption itemKey={"itemkey"+this.props.match.params.id}></CartOption>


	<button onClick={this.GoBack.bind(this)} >Go Back</button>
	</div>
	);
	}
}

function DetProduct(props)
{
	return (
		<img src={props.prod.thumbnailUrl} alt="image" width="300" height="300"></img>
	);
}

class CartOption extends React.Component{
	constructor(props)
	{
		super(props);
		this.state={Count:Shopping.getCartItem(this.props.itemKey)};
		this.addItem=this.addItem.bind(this);
		this.removeItem=this.removeItem.bind(this);
	}
	addItem()
	{
		Shopping.addCartItem(this.props.itemKey)
		const oCount=this.state.Count;
		this.setState({Count:oCount+1});
	}
	removeItem()
	{
		Shopping.removeCartItem(this.props.itemKey)
		const oCount=this.state.Count;
		this.setState({Count:oCount-1});
	}
	render(){
		return(<div>
			<button onClick={this.addItem}>Add({this.state.Count})</button>
			{this.state.Count>0 && <button onClick={this.removeItem}>Remove({this.state.Count})</button>}
		</div>);
	}


}

