import React from 'react';
import {NavLink} from 'react-router-dom';
import {Shopping} from '../Authroute';

export class ShopCart extends React.Component{
    constructor(props)
    {
        super(props);
    }

    render(){
        const dic=Shopping.GetCart();
        return (<div className="carttable">
                <div><span className="cartl1">Product Item</span> <span className="cartl2">Quantity</span> </div>  
          {dic.map((key,value)=><div>
               <span className="cartl1">{key}</span> 
               <span className="cartl2">{value}</span> </div>
            )}

           <NavLink to="/UserEntry">Check Out</NavLink>	 
        </div>);
    }
}


